import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import db from "./db.js";
import Typography from "@material-ui/core/Typography";

function titleCase(str) {
  return str.toLowerCase().replace(/\b(\w)/g, (s) => s.toUpperCase());
}

function DataTable({ email }) {
  //let email2 = "nithitam_thisa@cmu.ac.th";
  let db2 = db.find((el) => (el["Email Address"] === email ? true : false));
  let dbAll = db.find((el) => (el["First Name"] === "All" ? true : false));

  let rowName = [
    "1.1",
    "1.2",
    "1.3",
    "1.4",
    "1.5",
    "2.1",
    "2.2",
    "2.3",
    "2.4",
    "2.5",
    "3.1",
    "3.2",
    "3.3",
    "3.4",
    "3.5",
    "4.1",
    "4.2",
    "4.3",
    "4.4",
    "4.5",
    "4.6",
    "5.1",
    "5.2",
    "5.3",
    "5.4",
    "5.5",
    "5.6",
    "5.7",
    "5.8",
    "6",
    "Total",
    "Assignment",
  ];

  let fullName = "";
  let tableComponent = null;

  if (db2 !== undefined) {
    fullName = titleCase(db2["First Name"] + " " + db2["Last Name"]);
    tableComponent = (
      <div>
        <Typography variant="h3">{fullName}</Typography>
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Problem</TableCell>
                <TableCell>Full Score</TableCell>
                <TableCell>Your Score</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {rowName.map((el) => (
                <TableRow key={el}>
                  <TableCell>{el}</TableCell>
                  <TableCell>{dbAll[el]}</TableCell>
                  <TableCell>{db2[el]}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </div>
    );
  }

  return tableComponent;
}

export default DataTable;
